/* CalculatorApp.css */

/* 테블릿 화면 스타일 */
.calculator-app {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh; /* 화면 세로 중앙 정렬 */
   background-color: #f0f0f0; /* 배경색 추가 */
   box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); /* 그림자 추가 */
}

/* 계산기 컨테이너 스타일 */
.calculator-container {
   width: 80%;
   max-width: 400px; /* 계산기의 최대 너비 설정 */
   background-color: #fff;
   border: 2px solid #ccc;
   border-radius: 10px;
   padding: 20px;
}

/* 스크린 스타일 */
input {
   width: 94%;
   font-size: 24px;
   padding: 10px;
   text-align: right;
   margin-bottom: 10px;
   background-color: #f0f0f0; /* 스크린 배경색 변경 */
   border: 2px solid #ccc;
   border-radius: 5px;
}

/* 버튼 컨테이너 스타일 */
table {
   width: 100%;
}

/* 버튼 스타일 */
button {
   width: 25%;
   padding: 10px;
   font-size: 18px;
   border: none;
   background-color: #ffcccb;
   color: #333;
   cursor: pointer;
   transition: background-color 0.3s ease;
}

/* 버튼 호버 효과 */
button:hover {
   background-color: #ff6b6b;
   color: white;
}

/* '=' 버튼 스타일 */
button[onclick="calcResult"] {
   background-color: #6bff6b;
   color: white;
}

/* 'C' 버튼 스타일 */
button[onclick="calcInit"] {
   background-color: #ff6b6b;
   color: white;
}
